.rakuie-omakase {
  margin: 150px 0 0;
  padding: 100px 60px 60px;
  border: 2px solid #e45213;
  position: relative;
}

.rakuie-omakase h2 {
  position: absolute;
  top: -55px;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  font-size: 4.2rem;
  line-height: 1.3;
  font-weight: bold;
  background: #fff;
  padding: 0 20px;
  text-align: center;
  color: #e45213;
}

.rakuie-omakase p {
  font-size: 2.4rem;
  line-height: 1.6;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
  letter-spacing: 1px;
}

.rakuie-omakase p strong {
  font-size: 3.2rem;
  margin: 0 0.3em;
  background: linear-gradient(transparent 35%, #f6ee0f 75%);
}

.rakuie-omakase nav {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

.rakuie-omakase nav a {
  display: block;
  width: 300px;
  border-radius: 40px;
  padding: 19px 0 21px;
  background: #e45213;
  color: #fff;
  font-weight: bold;
  font-size: 1.8rem;
  text-align: center;
  text-decoration: none;
  position: relative;
}

.rakuie-omakase nav a::after {
  position: absolute;
  top: 50%;
  right: 20px;
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-top: 3px solid #fff;
  border-left: 3px solid #fff;
  transform: translateY(-50%) rotate(135deg);
}
